<template>
  <div class="wrapper">
    <AppBar
      :title="'Cluster Resources: ' + this.$route.params.id"
      :showBack="true"
      :isStraight="this.isStraight"
      @togglePathStyle="linkHandler"
    />
    <div
      id="chartLoadWrapper"
      v-if="treeIsReady"
    >
      <TargetClusterTree
        id="targetTree"
        :treeConfig="treeConfig"
        :treeData="treeData"
        :isStraight="isStraight"
        :legend="legend"
        @selectNode="selectNodeHandler"
        :style="{
          height: Object.keys(selected).length == 0 ? '100%' : 'calc(100% - 84px)' 
        }"
      />

      <div
        class="resourceView"
        v-if="resourceIsReady && this.selected.name"
      >
        <CustomResourceTree
          :items="resource"
          :title="'Resource: ' + selected.kind + '/' + selected.name"
          :color="legend[selected.provider].color"
          @unselectNode="(val) => { this.selected=val; }"
        />
      </div>
    </div>
    <div
      id="resourceTree"
      class="spinner"
      v-else
    >
      <v-progress-circular
        :size="50"
        :width="5"
        indeterminate
        color="primary"
      ></v-progress-circular>
    </div>
    <AlertError :message="errorMessage" />
  </div>
</template>

<script>
/* eslint-disable */
import TargetClusterTree from "../components/TargetClusterTree.vue";
import AppBar from "../components/AppBar.vue";
import CustomResourceTree from "../components/CustomResourceTree.vue";
import AlertError from "../components/AlertError.vue";

import colors from "vuetify/lib/util/colors";

import { getCluster, getClusterResource } from "../services/Service.js";

export default {
  name: "TargetCluster",
  components: {
    TargetClusterTree,
    AppBar,
    CustomResourceTree,
    AlertError,
  },
  mounted() {
    document.title = "Cluster Resources: " + this.$route.params.id;
  },
  methods: {
    linkHandler(val) {
      this.isStraight = val;
    },
    async selectNodeHandler(node) {
      if (node.provider == "") return;
      this.selected = node;
      try {
        const response = await getClusterResource(
          this.selected.group,
          this.selected.plural.toLowerCase(),
          this.selected.name
        );
        this.resource = response;
        this.resourceIsReady = true;
      } catch (error) {
        this.errorMessage =
          "Failed to fetch CRD for `" +
          this.selected.kind +
          this.selected.name +
          "`";
        console.log("Error fetching CRD");
        console.log(error);
      }
    },
    async fetchCluster() {
      try {
        const response = await getCluster(this.$route.params.id);
        this.treeData = response;
        this.treeIsReady = true;
      } catch (error) {
        this.errorMessage =
          "Failed to fetch resources for cluster `" +
          this.$route.params.id +
          "`";
        console.log(error);
      }
    },
  },
  async beforeMount() {
    await this.fetchCluster();
  },
  data() {
    return {
      errorMessage: "",
      treeIsReady: false,
      resourceIsReady: false,
      resource: [],
      selected: {},
      isStraight: false,
      treeData: {},
      treeConfig: { nodeWidth: 180, nodeHeight: 50, levelHeight: 120 },
      legend: {
        bootstrap: {
          name: "Bootstrap Provider",
          color: colors.amber.darken2,
        },
        controlplane: {
          name: "Control Plane Provider",
          color: colors.purple.darken1,
        },
        infrastructure: {
          name: "Infrastructure Provider",
          color: colors.green.base,
        },
        cluster: {
          name: "Cluster API",
          color: colors.blue.darken1,
        },
        addons: {
          name: "Addons",
          color: colors.red.darken1,
        },
        "": {
          name: "None",
          color: colors.grey.darken1,
        },
      },
    };
  },
};
</script>

